import React, {Component} from 'react';
import './Informacion.css';

class Informacion extends Component {
    
    render() { 
        return ( 
            <section className="row">
                <div className="col-sm-12">
                    <div className="text-center mt-2">
                        <img className="img-fluid" src="/img/kaziuz-live.jpg" alt="logo imagen" />
                    </div>
                    <div className="descripcion">
                        <p>
                            Me considero una persona prudente y sencilla, todos los das estoy aprendiendo algo nuevo. 
                            Amante a los videojuegos, la msica y la tecnologa. Me gusta 
                            trabajar en grupo. Conciente de que los grandes proyectos  se logran
                            con trabajo constante y paciencia. Me gusta el ciber punk, hacer msica, salir a caminar y ver pelculas
                            de ciencia ficcin.
                        </p>
                    </div>    
                </div>
            </section>
         );
    }
}
 
export default Informacion;


